/*** The new CSS Reset - version 1.2.0 (last updated 23.7.2021) ***/
/* Remove all the styles of the "User-Agent-Stylesheet", except for the 'display' property */
*:where(:not(iframe, canvas, img, svg, video):not(svg *)) {
  all: unset;
  display: revert;
}

/* Preferred box-sizing value */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove list styles (bullets/numbers) */
ol, ul {
  list-style: none;
}

/* removes spacing between cells in tables */
table {
  border-collapse: collapse;
}

/* revert the 'white-space' property for textarea elements on Safari */
textarea {
  white-space: revert;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--default-font-style);
  font-size: var(--default-font-size);
  overflow-x: hidden;
}

:root {
  --default-font-style: "Poppins", sans-serif;
  --default-font-size: 18px;
}

:root {
  --cyan: hsl(180, 66%, 49%);
  --white: hsl(0, 0%, 100%);
  --dark-violet: hsl(257, 27%, 26%);
  --light-gray: hsl(230, 25%, 95%);
  --red: hsl(0, 87%, 67%);
  --gray: hsl(0, 0%, 75%);
  --grayish-violet: rgb(158, 154, 167);
  --very-dark-blue: hsl(255, 11%, 22%);
  --vary-dark-violet: hsl(260, 8%, 14%);
}

.footer__wrapper {
  background-color: var(--vary-dark-violet);
  padding: 3.5rem 0;
}
.footer__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.footer__logo {
  margin-bottom: 2rem;
}
.footer__nav {
  font-size: 16px;
  line-height: 2;
}
.footer__heading > h4 {
  font-weight: 900;
  margin-bottom: 1rem;
  color: var(--white);
}
.footer__unordered {
  margin-bottom: 2rem;
  color: var(--grayish-violet);
}
.footer__social {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 180px;
  margin-top: 1rem;
}

.header__wrapper {
  background-color: var(--white);
  position: relative;
}
.header__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 0;
}
.header__item-image > a {
  display: flex;
}
.header__item-button {
  cursor: pointer;
}
.header__item-button .bar {
  display: block;
  width: 25px;
  height: 3px;
  margin: 5px auto;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s linear;
  background-color: var(--grayish-violet);
}
.header__item-button.is_active > .bar:nth-child(2) {
  opacity: 0;
}
.header__item-button.is_active > .bar:nth-child(1) {
  transform: translateY(8px) rotate(45deg);
}
.header__item-button.is_active > .bar:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg);
}
.header__nav {
  display: flex;
  justify-content: center;
  text-align: center;
  background-color: var(--dark-violet);
  border-radius: 10px;
  position: absolute;
  left: 0;
  right: 0;
  margin-left: 1.25rem;
  margin-right: 1.25rem;
  transition: transform 0.3s ease-in-out;
  transform: translateY(-150%);
}
.header__nav-menu {
  padding: 2rem 0;
  width: 80%;
}
.header__nav-list:not(:last-child) {
  line-height: 3.5;
}
.header__nav-list:nth-child(3) {
  padding-bottom: 1rem;
}
.header__nav-list:nth-child(4) {
  border-top: 1px solid rgba(158, 154, 167, 0.2);
  padding-top: 1rem;
}
.header__nav-list:nth-child(5) {
  background-color: var(--cyan);
  border-radius: 50px;
  padding: 0.5rem 0;
  cursor: pointer;
}
.header__nav-list > .header__nav-anchor {
  font-size: var(--default-font-size);
  font-weight: 700;
  color: var(--white);
  cursor: pointer;
}
.header__nav.is_active {
  transform: translateY(0);
  transition: transform 0.3s ease-in-out;
}

.container {
  width: min(90rem, 100%);
  margin-left: auto;
  margin-right: auto;
  padding-left: 1.35rem;
  padding-right: 1.35rem;
}
@media (min-width: 48em) {
  .container {
    padding-left: 2.5rem;
    padding-right: 2.5rem;
  }
}

.card__wrapper {
  padding-top: 10rem;
  padding-bottom: 1rem;
  background-color: var(--light-gray);
}
.card__title {
  text-align: center;
  margin-bottom: 1rem;
}
.card__title > h2 {
  font-size: clamp(28px, 4vw, 56px);
  font-weight: 900;
  color: var(--very-dark-blue);
  letter-spacing: -1px;
}
.card__paragraph {
  text-align: center;
  margin-bottom: 5rem;
}
.card__paragraph > p {
  font-size: 16px;
  text-align: center;
  line-height: 1.75;
  color: var(--grayish-violet);
}
.card__item {
  background-color: var(--white);
  margin-bottom: 5rem;
  border-radius: 5px;
  text-align: center;
  padding: 2rem 1.5rem;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
}
.card__item-icon {
  width: 50px;
  height: 50px;
  padding: 0.25rem;
}
.card__item-icon.--wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--dark-violet);
  border-radius: 50%;
  width: 80px;
  height: 80px;
  position: absolute;
  top: -40px;
}
.card__item-title {
  font-size: clamp(22px, 4vw, 56px);
  font-weight: 900;
  color: var(--very-dark-blue);
  margin-bottom: 1rem;
  margin-top: 3rem;
}
.card__item-paragraph {
  font-size: 16px;
  line-height: 1.75;
  color: var(--grayish-violet);
}
.card__item:last-child {
  margin-bottom: 3.5rem;
}
.card__item::after {
  content: "";
  background-color: var(--cyan);
  width: 8px;
  height: 80px;
  position: absolute;
  bottom: -80px;
}
.card__item:last-child::after {
  display: none;
}

.cta__wrapper {
  background: url("../../../images/mobile/bg-boost-mobile.svg"), var(--dark-violet);
  background-repeat: no-repeat;
  padding: 5rem 0;
}
.cta__title {
  text-align: center;
}
.cta__title > h2 {
  font-size: clamp(28px, 4vw, 56px);
  font-weight: 900;
  color: var(--white);
}
.cta__button {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}
.cta__button > button {
  border-radius: 50px;
  font-weight: 700;
  color: var(--white);
  background-color: var(--cyan);
  padding: 0.875rem 2.25rem;
}

.banner__wrapper {
  padding-bottom: 10rem;
}
.banner__image {
  width: 100%;
  position: relative;
  z-index: -1;
  margin-bottom: 2.5rem;
}
.banner__image > img {
  width: 450px;
  height: 100%;
}
@media (min-width: 30em) {
  .banner__image > img {
    width: 100%;
  }
}
.banner__title {
  margin-bottom: 1rem;
  text-align: center;
}
.banner__title > h1 {
  font-size: clamp(40px, 5vw, 72px);
  font-weight: 900;
  line-height: 1.2;
  letter-spacing: -1px;
  color: var(--very-dark-blue);
}
.banner__paragraph {
  margin-bottom: 2rem;
}
.banner__paragraph > p {
  text-align: center;
  line-height: 1.75;
  color: var(--grayish-violet);
}
.banner__cta {
  display: flex;
  justify-content: center;
}
.banner__cta > button {
  border-radius: 50px;
  font-weight: 700;
  color: var(--white);
  background-color: var(--cyan);
  padding: 0.875rem 2.25rem;
}

.shorten {
  position: absolute;
  width: 100%;
  transform: translateY(-90px);
}
.shorten__container {
  background: url("../../../images/mobile/bg-shorten-mobile.svg") right 0, var(--dark-violet);
  background-repeat: no-repeat;
  padding: 1.5rem;
  border-radius: 10px;
}
.shorten__form > input {
  width: 100%;
  border-radius: 10px;
  background-color: var(--white);
  padding: 0.875rem 1rem;
  margin-bottom: 1rem;
  font-size: var(--default-font-size);
}
.shorten__btn {
  background-color: var(--cyan);
  padding: 0.875rem 0;
  text-align: center;
  border-radius: 10px;
  cursor: pointer;
}
.shorten__btn > button {
  font-weight: 900;
  color: var(--white);
}